.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.center-block {
  display: block;
  margin-right: auto;
  margin-left: auto;
}
.fr {
  float: right !important;
}
.fl {
  float: left !important;
}
.hide,
.hidden {
  display: none !important;
}
.show {
  display: block !important;
}
.visible {
  visibility: visible;
}
.invisible {
  visibility: hidden;
}
.ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.usn {
  user-select: none;
}
.bdbox {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

body.landscape {
  background: #f60;
}
body.landscape * {
  visibility: hidden !important;
}
body.landscape:after {
  z-index: 1;
  position: absolute;
  content: '请使用竖屏浏览';
  color: #fff;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0);
}

.loading {
  position: fixed;
  left: 50%;
  top: 50%;
  background: rgba(0, 0, 0, 0.8);
  z-index: 100000;
  padding: 8px;
  margin-left: -25px;
  margin-top: -25px;
  border-radius: 5px;
}
@-webkit-keyframes loading {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
@-webkit-keyframes loading {
  100% {
    -webkit-transform: rotate(360deg);
  }
}
.loading:after {
  display: block;
  content: '';
  background: url("data:image/svg+xml;charset=utf-8,<svg%20viewBox%3D'0%200%20120%20120'%20xmlns%3D'http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg'%20xmlns%3Axlink%3D'http%3A%2F%2Fwww.w3.org%2F1999%2Fxlink'><defs><line%20id%3D'l'%20x1%3D'60'%20x2%3D'60'%20y1%3D'7'%20y2%3D'27'%20stroke%3D'%23fff'%20stroke-width%3D'11'%20stroke-linecap%3D'round'%2F><%2Fdefs><g><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(30%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(60%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(90%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(120%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.27'%20transform%3D'rotate(150%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.37'%20transform%3D'rotate(180%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.46'%20transform%3D'rotate(210%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.56'%20transform%3D'rotate(240%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.66'%20transform%3D'rotate(270%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.75'%20transform%3D'rotate(300%2060%2C60)'%2F><use%20xlink%3Ahref%3D'%23l'%20opacity%3D'.85'%20transform%3D'rotate(330%2060%2C60)'%2F><%2Fg><%2Fsvg>") no-repeat 50% 50%;
  background-size: 100%;
  left: 50%;
  top: 50%;
  width: 34px;
  height: 34px;
  -webkit-transform-origin: 50%;
  transform-origin: 50%;
  -webkit-animation: loading 1s steps(12, end) infinite;
  animation: loading 1s steps(12, end) infinite;
}
.loading-x2 {
  padding: 16px;
  margin-left: -50px;
  margin-top: -50px;
  border-radius: 10px;
}
.loading-x2:after {
  width: 68px;
  height: 68px;
}
.loading-overlay:before {
  position: fixed;
  content: '';
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.loading-hide {
  display: none;
}

html,
body,
div,
span,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
abbr,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
samp,
small,
strong,
sub,
sup,
var,
b,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  outline: 0;
  border: 0;
  background: transparent;
  vertical-align: baseline;
  font-style: inherit;
  font-size: 100%;
}
html {
  height: 100%;
  -webkit-tap-highlight-color: transparent;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  margin: 0 auto;
  max-width: 640px;
  height: 100%;
  line-height: 1;
  font-size: 0.32rem;
  font-family: 'Helvetica', 'Hiragino Sans GB W3', arial, sans-serif;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
a:active {
  outline: 0;
}
button[disabled],
html input[disabled] {
  cursor: default;
}
img {
  vertical-align: middle;
}
button,
input,
select,
textarea {
  margin: 0;
  padding: 0;
  font-size: 100%;
  font-family: inherit;
}
button,
input {
  line-height: normal;
}
button,
select {
  text-transform: none;
}
button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  cursor: pointer;
  -webkit-appearance: button;
}
input[type="search"] {
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
dfn {
  font-style: italic;
}
code,
kbd,
pre,
samp {
  font-size: 1em;
  font-family: monospace, serif;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}

#index {
  height: 100%;
}
#index #container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
#index #container ul {
  height: 100%;
}
#index #container li {
  width: 100%;
  height: 100%;
  position: relative;
  top: 0%;
  left: 0%;
}
#index #canvas {
  width: 6.4rem;
  height: 10.39rem;
}
#index .btn_music {
  position: absolute;
  top: 0.40rem;
  right: 0.40rem;
  /* display:none; */
  width: 0.66rem;
  height: 0.66rem;
  background: url(default/page/index/images/music.png) 0 0 no-repeat;
  -webkit-background-size: 100% auto;
  background-size: 100% auto;
}
#index .music_off {
  background-position: 0 100%;
}
#index .qrcode {
  position: absolute;
  top: 6.72rem;
  left: 1.32rem;
  display: none;
  width: 1.7rem;
}
#index .arrow {
  position: absolute;
  bottom: 0.48rem;
  left: 50%;
  display: none;
  width: 0.48rem;
  margin-left: -0.24rem;
  -webkit-animation: anim_arrow 1s ease-in-out infinite;
  animation: anim_arrow 1s linear infinite;
}
@-webkit-keyframes anim_arrow {
  0% {
    -webkit-transform: translate(0, -6px);
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  90% {
    -webkit-transform: translate(0, 6px);
    opacity: 0;
  }
  100% {
    -webkit-transform: translate(0, 6px);
    opacity: 0;
  }
}
@keyframes anim_arrow {
  0% {
    transform: translate(0, -6px);
    opacity: 0;
  }
  25% {
    opacity: 1;
  }
  75% {
    opacity: 1;
  }
  90% {
    transform: translate(0, 6px);
    opacity: 0;
  }
  100% {
    transform: translate(0, 6px);
    opacity: 0;
  }
}
